@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600;700&display=swap');
*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --background: #fff;
  /* --card-background-color: linear-gradient(135deg, #8BC6EC 0%, #9599E2 100%); */

  --btn-color: hsla(0, 0%, 2%, 1);
  --text-primary: hsla(0, 0%, 100%, 1);
  --text-secondary: hsla(0, 0%, 83%, 1);
  --border-color: white;
  /* --card-shadow:4px 7px 19px rgba(168, 44, 210, 0.56); */
}

